.room{
    position: absolute;
    background-color: rgb(58, 58, 58);
    border: 4px solid rgb(0, 0, 0);
    margin: 4px;
    z-index: 2;
}

.openRoom{
    background-color: rgb(162, 167, 119);
}

.startRoom{
    background-color: green;
}

.endRoom{
    background-color: rgb(1, 26, 255);
}